.landing-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.landing-header {
  background-color: #ffffff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  padding: 1.2rem 0;
}

.header-content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-content h1 {
  color: #1a2b3c;
  margin: 0;
  font-size: 2rem;
  font-weight: 600;
}

.auth-buttons {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.login-button,
.register-button {
  padding: 0.6rem 1.8rem;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-button {
  background-color: transparent;
  color: #0052cc;
  border: 1px solid #0052cc;
}

.login-button:hover {
  background-color: #0052cc;
  color: #ffffff;
  transform: translateY(-1px);
}

.register-button {
  background-color: #0052cc;
  color: #ffffff;
}

.register-button:hover {
  background-color: #003d99;
  transform: translateY(-1px);
}

.user-section {
  position: relative;
  display: flex;
  align-items: center;
}

.user-icon-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2rem;
  background-color: #f9fafb;
  border: 1px solid #e2e8f0;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 40px;
  height: 40px;
  overflow: hidden;
  position: relative;
}

.user-icon-button:hover {
  background-color: #edf2f7;
  border-color: #cbd5e1;
  transform: translateY(-1px);
}

.user-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.user-name {
  font-size: 0.9rem;
  color: #4a5568;
  margin-left: 0.5rem;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.user-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.12);
  padding: 0.8rem 0;
  min-width: 260px;
  z-index: 1000;
  margin-top: 0.6rem;
  border: 1px solid #e2e8f0;
}

.dropdown-header {
  display: flex;
  align-items: center;
  padding: 0.8rem 1.2rem;
  gap: 0.8rem;
  border-bottom: 1px solid #edf2f7;
}

.dropdown-user-photo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
}

.dropdown-user-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.dropdown-user-info {
  overflow: hidden;
}

.dropdown-user-name {
  font-weight: 600;
  font-size: 1rem;
  color: #1a2b3c;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown-user-email {
  font-size: 0.85rem;
  color: #718096;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown-divider {
  height: 1px;
  background-color: #e2e8f0;
  margin: 0.6rem 0;
}

.dropdown-item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.8rem 1.2rem;
  border: none;
  background: none;
  text-align: left;
  cursor: pointer;
  color: #1a2b3c;
  font-size: 0.9rem;
  gap: 0.8rem;
  transition: all 0.2s ease;
}

.dropdown-item:hover {
  background-color: #f7fafc;
}

.dropdown-item.logout {
  color: #c53030;
}

.dropdown-item-icon {
  font-size: 1.1rem;
  width: 20px;
  text-align: center;
  color: #718096;
}

.landing-content {
  flex: 1;
}

.hero-section {
  background: linear-gradient(135deg, #0052cc 0%, #1a2b3c 100%);
  color: #ffffff;
  padding: 4rem 2rem;
  text-align: center;
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero-content h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.cta-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.cta-primary,
.cta-secondary {
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cta-primary {
  background-color: #ffffff;
  color: #0052cc;
}

.cta-primary:hover {
  background-color: #f7fafc;
  transform: translateY(-2px);
}

.cta-secondary {
  background-color: transparent;
  color: #ffffff;
  border: 1px solid #ffffff;
}

.cta-secondary:hover {
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

.features-section {
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.features-section h3 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 3rem;
  color: #1a2b3c;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.feature-card {
  text-align: center;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.feature-card h4 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #1a2b3c;
}

.feature-card p {
  color: #718096;
  line-height: 1.6;
}

.landing-footer {
  background-color: #f9fafb;
  padding: 1.5rem;
  text-align: center;
  color: #718096;
  margin-top: auto;
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    gap: 1rem;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .hero-content h2 {
    font-size: 2rem;
  }

  .features-grid {
    grid-template-columns: 1fr;
  }

  .auth-buttons {
    flex-direction: column;
    width: 100%;
  }

  .login-button,
  .register-button {
    width: 100%;
  }

  .user-dropdown {
    right: auto;
    left: 0;
    min-width: 220px;
  }

  .user-icon-button {
    width: 36px;
    height: 36px;
  }

  .user-name {
    display: none;
  }
}